
import { defineComponent, ref, PropType } from "vue";
import Show from "@/types/Show";
export default defineComponent({
  props: {
    shows: {
      required: true,
      type: Object as PropType<Show[]>,
    },
  },
  setup() {
    return {
      slide: ref(1),
      ratingModel: ref(1),
    };
  },
  methods: {
    updateRating(rating: number) {
      this.ratingModel = Math.ceil(rating / 5) * 5;
    },
  },
});
